/* http://meyerweb.com/eric/tools/css/reset/
   v3.0 | 20180413
   License: none (public domain)
*/
html, body, div, span, applet, object, iframe,
h1, h2, h3, h4, h5, h6, p, blockquote, pre,
a, abbr, acronym, address, big, cite, code,
del, dfn, em, img, ins, kbd, q, s, samp,
small, strike, strong, sub, sup, tt, var,
b, u, i, center,
dl, dt, dd, ol, ul, li,
fieldset, form, label, legend,
table, caption, tbody, tfoot, thead, tr, th, td,
article, aside, canvas, details, embed,
figure, figcaption, footer, header, hgroup,
main, menu, nav, output, ruby, section, summary,
time, mark, audio, video {
  margin: 0;
  padding: 0;
  border: 0;
  font-size: 100%;
  font: inherit;
  vertical-align: baseline; }

/* HTML5 display-role reset for older browsers */
article, aside, details, figcaption, figure,
footer, header, hgroup, main, menu, nav, section {
  display: block; }

/* HTML5 hidden-attribute fix for newer browsers */
*[hidden] {
  display: none; }

body {
  line-height: 1; }

ol, ul {
  list-style: none; }

blockquote, q {
  quotes: none; }

blockquote:before, blockquote:after,
q:before, q:after {
  content: '';
  content: none; }

table {
  border-collapse: collapse;
  border-spacing: 0; }

@font-face {
  font-family: 'Oswald-Regular';
  src: url("../fonts/Oswald/Oswald-Regular.eot?v=4.6.2");
  src: url("../fonts/Oswald/Oswald-Regular.eot?#iefix&v=4.6.2") format("embedded-opentype"), url("../fonts/Oswald/Oswald-Regular.woff2?v=4.6.2") format("woff2"), url("../fonts/Oswald/Oswald-Regular.woff?v=4.6.2") format("woff"), url("../fonts/Oswald/Oswald-Regular.ttf?v=4.6.2") format("truetype"), url("../fonts/Oswald/Oswald-Regular.svg?v=4.6.2#OswaldRegular") format("svg");
  font-weight: normal;
  font-style: normal; }

@font-face {
  font-family: 'icomoon';
  src: url("../fonts/icomoon/icomoon.eot?tks2a0");
  src: url("../fonts/icomoon/icomoon.eot?tks2a0#iefix") format("embedded-opentype"), url("../fonts/icomoon/icomoon.ttf?tks2a0") format("truetype"), url("../fonts/icomoon/icomoon.woff?tks2a0") format("woff"), url("../fonts/icomoon/icomoon.svg?tks2a0#icomoon") format("svg");
  font-weight: normal;
  font-style: normal; }

[class^="icon-"], [class*=" icon-"] {
  /* use !important to prevent issues with browser extensions that change fonts */
  font-family: 'icomoon' !important;
  speak: none;
  font-style: normal;
  font-weight: normal;
  font-variant: normal;
  text-transform: none;
  line-height: 1;
  /* Better Font Rendering =========== */
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale; }

.icon-clapperboard:before {
  content: "\e900"; }

.icon-compass:before {
  content: "\e901"; }

.icon-transport:before {
  content: "\e902"; }

* {
  box-sizing: border-box;
  transition: background .3s, color .3s, filter .3s; }

a {
  color: inherit;
  text-decoration: none; }
  a:focus {
    outline: none;
    border-color: transparent; }

h1, figure, fieldset {
  margin: 0;
  padding: 0; }

input, select, textarea, option {
  appearance: none;
  -ms-appearance: none;
  border-radius: 0;
  border: 0;
  padding: 0;
  font-family: "Oswald-Regular", sans-serif; }
  input:focus, input:active, select:focus, select:active, textarea:focus, textarea:active, option:focus, option:active {
    outline: 0; }

:-ms-input-placeholder {
  font-family: "Oswald-Regular", sans-serif; }

::-moz-placeholder {
  font-family: "Oswald-Regular", sans-serif; }

::-webkit-input-placeholder {
  font-family: "Oswald-Regular", sans-serif; }

label {
  cursor: pointer;
  user-select: none; }

fieldset {
  border: none; }

html {
  font-size: 62.5%; }

body, html {
  width: 100%;
  height: 100%;
  line-height: 1;
  font-family: "Oswald-Regular", sans-serif; }

body {
  overflow-x: hidden;
  font-size: 1.4rem;
  height: 100%; }

button {
  cursor: pointer;
  font-family: inherit;
  color: inherit;
  appearance: none;
  border: 0;
  padding: 0;
  background: transparent;
  letter-spacing: 0.4px;
  line-height: 1;
  user-select: none; }
  button:focus, button:active {
    appearance: none;
    outline: none; }

img {
  max-width: 100%;
  vertical-align: bottom; }

.wrapper {
  height: auto;
  min-height: 100%;
  background: #2D6E68; }

.container {
  width: 100%;
  margin: 0 auto;
  padding: 0 20px; }
  @media screen and (min-width: 480px) {
    .container {
      max-width: 425px;
      padding: 0; } }
  @media screen and (min-width: 768px) {
    .container {
      max-width: 720px;
      padding: 0; } }

.slider {
  display: flex;
  align-items: stretch;
  padding: 10px 0;
  color: #ffffff; }
  @media screen and (max-width: 767px) {
    .slider {
      flex-direction: column; } }

.main-controls {
  display: inline-block;
  flex: 0 0 120px; }
  .main-controls .list {
    display: flex;
    height: 100%; }
    @media screen and (min-width: 768px) {
      .main-controls .list {
        flex-direction: column; } }
  .main-controls .item {
    overflow: hidden;
    margin-bottom: 10px; }
    @media screen and (min-width: 768px) {
      .main-controls .item:last-child {
        margin-bottom: 0; } }
    @media screen and (max-width: 767px) {
      .main-controls .item {
        width: 33.3333%; }
        .main-controls .item:not(:last-child) {
          margin-right: 10px; } }
  .main-controls .btn {
    background: #36CAE8;
    font-size: 1.4rem;
    color: #ffffff;
    width: 100%;
    height: 130px;
    display: flex;
    justify-content: center;
    align-items: center;
    flex-direction: column;
    padding-bottom: 15px; }
    .main-controls .btn:hover {
      background: #16a0bc; }
    .main-controls .btn .btn-ico {
      position: relative;
      width: 70px;
      height: 70px;
      overflow: hidden; }
      .main-controls .btn .btn-ico::before {
        content: '';
        font-family: "icomoon";
        font-size: 6rem;
        padding: 5px 0;
        color: inherit;
        position: absolute;
        top: 0;
        left: 0;
        width: 100%;
        display: flex;
        justify-content: center; }
  .main-controls .car .btn-ico::before {
    content: "\e902"; }
  .main-controls .movie .btn-ico::before {
    content: "\e900"; }
  .main-controls .location .btn-ico::before {
    content: "\e901"; }
  .main-controls .btn-value {
    width: 100%; }

.content-controls {
  overflow: hidden;
  height: 410px; }
  @media screen and (min-width: 768px) {
    .content-controls {
      flex: 2 2;
      margin: 0 10px; } }
  @media screen and (max-width: 767px) {
    .content-controls {
      max-height: 310px;
      margin-bottom: 10px; } }
  .content-controls .list {
    display: flex;
    flex-direction: column;
    height: 100%; }
  .content-controls .item {
    display: flex;
    justify-content: center;
    align-items: center; }
    .content-controls .item:not(:last-child) {
      padding-bottom: 10px; }
    @media screen and (min-width: 768px) {
      .content-controls .item {
        height: 25%; }
        .content-controls .item:last-child {
          margin-bottom: 0; } }
  .content-controls .btn {
    width: 100%;
    height: 100%;
    background: #F88451; }
    .content-controls .btn:hover {
      background: #f3510a; }
    @media screen and (max-width: 767px) {
      .content-controls .btn {
        min-height: 70px; } }

.slide-content {
  background: #78EAD3;
  padding: 10px;
  color: #333333; }
  @media screen and (max-width: 767px) {
    .slide-content {
      min-height: 200px; } }
  @media screen and (min-width: 768px) {
    .slide-content {
      flex: 2 2; } }
